.menu {
	position: relative;
	padding: 45px 30px 65px;
	box-shadow: 0 2px 9px 0 rgba($black, 0.06);
	background: $twilight;
	background-image: linear-gradient(249deg, $twilight, $dark-grey-blue);
	@include lg {
		padding: 15px 20px;
		overflow: visible; }
	&__profile {
		margin-bottom: 48px;
		@include lg {
			margin-bottom: 0;
			display: inline-block;
			vertical-align: top; } }
	&__meta {
		@include lg {
			display: inline-block;
			vertical-align: top;
			margin: 8px 0 0 30px; }
		@include xs {
			display: block;
			margin: 15px 0 0; }
		&-title {
			text-transform: uppercase;
			color: $white;
			margin-bottom: 19px;
			letter-spacing: .1px;
			@include f(12px, 600, 1.42);
			@include lg {
				display: inline-block;
				vertical-align: top;
				margin-bottom: 0;
				@include f(13px, null, 1.7); } }
		&-label {
			display: inline-block;
			color: rgba($white, .9);
			text-decoration: none;
			@include f(13px, null, 1.7);
			@include lg {
				display: inline-block;
				vertical-align: top;
				margin-left: 10px; } } }
	&__logout {
		position: absolute;
		left: 30px;
		bottom: 35px;
		font-size: 0;
		@include lg {
			left: auto;
			bottom: auto;
			top: 50%;
			right: 20px;
			transform: translateY(-50%); }
		@include xs {
			top: 15px;
			transform: translateY(0); }
		&-label {
			display: inline-block;
			vertical-align: middle;
			color: $white;
			@include f(13px, null, 1.7); }
		&-icon {
			display: inline-block;
			vertical-align: middle;
			margin-left: 7px;
			font-size: 15px; } } }
