body {
	min-width: 320px;
	background: #FFF;
	color: #002257;
	font-family: $opensans;
	@include f(15px, 400, 1.4); }

.out {
	display: flex;
	@include lg {
		flex-wrap: wrap; }
	@include md {
		display: block; } }

.menu,
.aside,
.main {
	overflow: auto;
	height: 100vh; }

.aside,
.main {
	@include lg {
		height: calc(100vh - 70px); }
	@include md {
		height: auto; } }

.menu {
	width: 244px;
	@include lg {
		padding: 0;
		width: 100%;
		height: 70px; }
	@include md {
		height: auto; } }

.aside {
	width: 400px; }

.main {
	flex-grow: 1; }

.title-lg {
	color: #37394C;
	@include f(24px, 400, 33px); }

[hidden],
.is-hidden {
	display: none !important; }
