.main {
	padding: 40px 32px 18px;
	background: #f8f9fb;
	@include md {
		padding: 40px 20px; }
	&__inner {
		margin: 0 auto;
		max-width: 754px;
		@include clr; }
	&__header {
		margin-bottom: 38px;
		@include clr; }
	&__title {
		float: left;
		padding: 2px 32px 5px 0;
		@include sm {
			float: none;
			padding: 0 0 20px; } }
	&__search {
		position: relative;
		float: left;
		padding-left: 33px;
		width: 100%;
		max-width: 408px;
		@include sm {
			float: none;
			padding: 22px 0 0 0;
			max-width: none; }
		&:before {
			content: '';
			position: absolute;
			top: 4px;
			left: 0;
			bottom: 4px;
			border-left: 1px solid rgba(#535285, 0.06);
			@include sm {
				top: 0;
				left: 0;
				bottom: auto;
				border-top: 1px solid rgba(#535285, 0.1);
				border-left: none;
				width: 60px; } } }
	&__footer {
		padding-top: 17px;
		@include clr;
		@include xs {
			padding-top: 20px; } }
	&__page {
		padding: 7px 120px;
		color: #849BB0;
		@include f(13px, 400, 18px);
		text-align: center;
		@include xs {
			overflow: hidden;
			padding-right: 20px;
			padding-left: 0;
			text-align: left; } }
	&__next {
		float: right; } }
