.simple-profile {
	&__img {
		float: left;
		border-radius: 50%;
		width: 60px;
		height: 60px;
		@include cover; }
	&__content {
		overflow: hidden;
		padding: 8px 0 8px 20px; }
	&__title {
		margin-bottom: 2px;
		@include f(15px, 600, 20px);
		letter-spacing: 0.1px; }
	&__meta {
		color: #97A3B4;
		@include f(13px, 400, 22px); } }
