@mixin col($name) {
	&[data-column="#{$name}"] {
		@content; } }

$colName: 'name';
$colFruquency: 'frequency';
$colAddress: 'address';
$colStatus: 'status';

$tableWidth: 7.54;

.table {
	font-size: 0;
	&__thead {
		@include sm {
			display: none; } }
	&__th {
		display: inline-block;
		vertical-align: top;
		padding-bottom: 8px;
		color: #AAB2C0;
		@include f(11px, 600, 15px);
		text-transform: uppercase; }
	&__th,
	&__td {
		padding-left: 15px;
		padding-right: 15px;
		@include clr;
		@include col($colName) {
			padding-left: 18px;
			width: (210/$tableWidth) + %;
			@include sm {
				padding-bottom: 20px; } }
		@include col($colFruquency) {
			width: (114/$tableWidth) + %; }
		@include col($colAddress) {
			width: (315/$tableWidth) + %; }
		@include col($colStatus) {
			padding-right: 60px;
			width: (115/$tableWidth) + %;
			@include sm {
				padding-right: 15px; } } }
	&__td {
		display: inline-block;
		vertical-align: middle;
		@include f(15px, 400, 22px);
		@include r(900) {
			@include f(13px, null, 1.46); }
		@include sm {
			display: block;
			padding-top: 3px;
			padding-bottom: 3px;
			width: 100% !important;
			@include f(15px); } }
	&__item {
		position: relative;
		display: block;
		padding-top: 17px;
		padding-bottom: 17px;
		border: 1px solid #E6EAEE;
		border-radius: 4px;
		color: inherit;
		background: $white;
		text-decoration: none;
		transition: border-color .2s, background .2s;
		@include sm {
			padding-right: 30px; }
		&:not(:last-child) {
			margin-bottom: 8px; }
		@include hover {
			border-color: rgba(84,90,191,0.58);
			background: #F6FBFD; } }
	&__img {
		float: left;
		border-radius: 50%;
		width: 38px;
		height: 38px;
		@include cover;
		@include r(900) {
			margin-top: 0; } }
	&__content {
		overflow: hidden;
		padding-left: 18px;
		margin-top: -2px;
		@include r(900) {
			padding-top: 2px;
			padding-bottom: 2px; }
		@include sm {
			padding-top: 0;
			padding-bottom: 0; } }
	&__title {
		@include f(15px, 600, 22px);
		@include r(900) {
			@include f(13px, null, 1.46); }
		@include sm {
			@include f(15px); } }
	&__meta {
		color: #97A3B4;
		@include f(13px, 400, 18px);
		@include r(900) {
			@include f(12px, null, 1.385); }
		@include sm {
			@include f(13px); } }
	&__arrow {
		position: absolute;
		top: 50%;
		right: 29px;
		margin-top: -0.5em;
		fill: #B1BAC7;
		font-size: 17px;
		@include xs {
			right: 15px; } }
	&__status {
		display: block;
		margin: 0 auto;
		border: 2px solid #FFFFFF;
		border-radius: 50%;
		width: 12px;
		height: 12px;
		background: #59C78A;
		box-shadow: 0 0 0 1px #59C78A;
		@include sm {
			display: inline-block;
			vertical-align: top;
			margin-top: 5px; }
		&.is-error {
			background: #F05692;
			box-shadow: 0 0 0 1px #F05692; } }
	&__label {
		display: none;
		padding-right: 5px;
		color: #AAB2C0;
		font-weight: 600;
		text-transform: uppercase;
		@include sm {
			display: inline; } }
	&__value {
		@include sm {
			display: inline; } } }
